.screen{
    position: relative;
    width: 591px;
    left: 0%;
    background-color: rgba(255, 0, 0, 0);
    height: 1280px;
    z-index: 0;
}
.flash{
   
    width: 100%;
    height: 100%;
    position: relative;
    left: 0%;
    top: -50px;
    z-index: 2;
    background-color: rgb(13, 5, 102);
}
.massig{
    position: absolute;
    z-index: 3;
    top: 40%;
    left: 5%;
    width: 40%;
    padding: 10px;
    padding-bottom: 0px;
border-radius: 8px;
height: auto;
border: none;
background-color: rgba(240, 248, 255, 0.148);
backdrop-filter: blur(10px);
color: aliceblue;
}
.massig b{
    font-size: 13px;
    position: relative;
    top: -10px;
}
.massig p{
    font-size: 10px;
    position: relative;
    top: -5px;
}
.massig span{
    font-size: 10px;
    position: relative;
    top: -40px;
    left: 95%;
}
.timescreen{
    text-align: center;
    color: aliceblue;
    width: 30%;
    position: relative;
  
    top: 20%;
    z-index: 4;
    left: 10%;
}
.timescreen p{
    text-align: center;
    color: aliceblue;
 font-size: 10px;
}